.contact .fab, .contact .fas {
    font-size: 3em;
}

.contact div a {
    color: white;
    text-align: center;
    margin: 0px 25px;
}

.contact .fab:hover, .contact .fas:hover {
    transform: scale(1.5);
}

.contact .fab, .contact .fas {transition: transform .25s}

.contact-icons {
    margin: auto;
}

@media screen and (max-width: 425px) {
    .contact .fab, .contact .fas {
        font-size: 2.5em;
    }
}